<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="addCategoryCourse">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small">添加原子课</el-button>
                <el-button type="primary" icon="el-icon-plus" size="small">添加课程包</el-button>
            </div>
            <div>
                <el-input
                    placeholder="原子课名称或ID"
                    size="small"
                    style="width: 140px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <el-input
                    placeholder="课程包名称或ID"
                    size="small"
                    style="width: 140px"
                    v-model="searchParams.postTitle"
                    clearable>
                </el-input>
                <SelectTags v-model="searchParams.tags"></SelectTags>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="售卖类型"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="是否免费"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-button type="success"  size="small" @click="">查询</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="课程名称"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="运营标签">
            </el-table-column>
            <el-table-column
                prop="address"
                label="售卖类型">
            </el-table-column>
            <el-table-column
                prop="address"
                label="限时免费">
            </el-table-column>
            <el-table-column
                prop="address"
                label="售卖金额">
            </el-table-column>
            <el-table-column
                prop="address"
                label="折扣类型">
            </el-table-column>
            <el-table-column
                prop="address"
                label="发布时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="280">
                <template slot-scope="scope">
                    <el-button @click="handleClick(scope.row)" type="info" size="small"
                               >置顶</el-button>
                    <el-button @click="handleClick(scope.row)" type="primary" size="small"
                               >预览</el-button>
                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                               >移除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'
    import SelectTags from '@/components/SelectTags/SelectTags.vue';

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem ipsum dolor sit amet, consectetur adipisicing elit. Asperiores fugit in quae vero. Adipisci blanditiis dignissimos eum facere laudantium quasi ratione repellat vitae! Alias consequatur dolores enim neque similique unde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination,SelectTags
        }
    }
</script>
